    'use client'
    import Image from 'next/image';
    import { useState, useEffect } from 'react';
    import { FaGoogle } from 'react-icons/fa';
    import './login.css';
    import image5 from '../../images/login/a1.jpg'; // Ensure this path is correct
    const LoginPage = () => {
    const [backgroundIndex, setBackgroundIndex] = useState(0);
    const backgrounds = [
        '../../images/login/a1.jpg',
        '../../images/login/a1.jpg',  
        '/images/landingpage/image6.jpeg',  
    ];

    useEffect(() => {
        const interval = setInterval(() => {
        setBackgroundIndex((prevIndex) => (prevIndex + 1) % backgrounds.length);
        }, 5000);

        // Cleanup the interval on component unmount
        return () => clearInterval(interval);
    }, []); // The empty dependency array ensures this effect runs only once


    return (
        <div className="login-container">
        
        <div className="left-section">
            {/* Use Image tag for background */}
            <Image
            src={backgrounds[backgroundIndex]}
            alt="Background"
            layout="fill" // Make the image cover the full div
            objectFit="cover" // Ensures the image covers the div
            objectPosition="center" // Centers the image
            quality={100} // Optional: To ensure high quality
            />
        </div>



        <div className="right-section">
            <h1 className="title">Login</h1>

            <form className="login-form">
            <div className="form-group">
                <label htmlFor="email">Email Address</label>
                <input type="email" id="email" name="email" placeholder="Enter your email" />
            </div>

            <div className="form-group">
                <label htmlFor="password">Password</label>
                <input type="password" id="password" name="password" placeholder="Enter your password" />
            </div>

            <button type="submit" className="login-button">
                Login
            </button>

            <div className="signup-prompt">
                <span>Don't have an account?</span>
                <a href="/signup">Sign Up</a>
            </div>

            <button type="button" className="google-signup-button">
                <FaGoogle className="google-icon" />
                <span>Sign Up with Google</span>
            </button>
            </form>
        </div>
        </div>
    );
    };

    export default LoginPage;
